<template>
  <div class="position-relative">
    <svg viewBox="0 0 400 400" xmlns="http://www.w3.org/2000/svg">
      <line v-if="a" y2="50%" x2="50%" y1="0" x1="0" :stroke-width="strokeWidth" :stroke="color"/>
      <line v-if="b" y2="50%" x2="50%" y1="0" x1="50%" :stroke-width="strokeWidth" :stroke="color"/>
      <line v-if="c" y2="50%" x2="50%" y1="0" x1="100%" :stroke-width="strokeWidth" :stroke="color"/>
      <line v-if="d" y2="50%" x2="50%" y1="50%" x1="100%" :stroke-width="strokeWidth" :stroke="color"/>
      <line v-if="e" y2="50%" x2="50%" y1="100%" x1="100%" :stroke-width="strokeWidth" :stroke="color"/>
      <line v-if="f" y2="50%" x2="50%" y1="100%" x1="50%" :stroke-width="strokeWidth" :stroke="color"/>
      <line v-if="g" y2="50%" x2="50%" y1="100%" x1="0" :stroke-width="strokeWidth" :stroke="color"/>
      <line v-if="h" y2="50%" x2="50%" y1="50%" x1="0" :stroke-width="strokeWidth" :stroke="color"/>
    </svg>
    <f7-button :class="value < 0 ? 'test-button animated pulse infinite' : 'test-button'" fill big v-if="circle" v-on:click="updateCount">
      <h1 :class="'test-h1'">{{ value }} <span style="font-size: 10px; position: absolute;">{{rowId}}-{{colId}}</span></h1>
    </f7-button>
  </div>
</template>

<script>
  export default {
    name: 'svg-circle-full',
    props: ['rowId', 'colId', 'value', 'data', 'share', 'callback'],
    data() {
      return {
        color: '#F98E6D',
        fontSize: 120,
        strokeWidth: 50,
        circle: null,
        a: null,
        b: null,
        c: null,
        d: null,
        e: null,
        f: null,
        g: null,
        h: null,
      }
    },
    mounted() {
      this.setData()
    },
    methods: {
      setData() {
        let data = this.data.split('');
        if (data.indexOf('r') !== -1) this.circle = true;
        if (data.indexOf('e') !== -1) this.e = true; //  \
        if (data.indexOf('a') !== -1) this.a = true; // \
        if (data.indexOf('g') !== -1) this.g = true; // /
        if (data.indexOf('c') !== -1) this.c = true; //  /
        if (data.indexOf('d') !== -1) this.d = true; //  -
        if (data.indexOf('h') !== -1) this.h = true; // -
        if (data.indexOf('f') !== -1) this.f = true; // |
        if (data.indexOf('b') !== -1) this.b = true; // |

        // ea      bf  dh --
        //  e\  /g  |
        //   a\/ c  |
        //    /\
        //   /  \
        //  cg   ea
      },
      updateCount() {
        if (this.circle) {
          // this.updateAnimation();

          let data = this.share.split(',');
          let grid = [];
          for (let i = 0; i < data.length; i++) {
            grid.push(data[i].split('-').map(x => parseInt(x)));
          }

          if(typeof this.callback === 'function'){
            this.callback([this.rowId, this.colId], grid, this.value);
          }
        }
      },
      updateAnimation() {
        let h1s = document.getElementsByClassName('test-h1');
        for (let i = 0; i < h1s.length; i++) {
          h1s[i].classList.remove('animated', 'rollIn');
          setTimeout(() => h1s[i].classList.add('animated', 'rollIn'), 50);
        }
      }
    }
  }
</script>



