<template>
  <f7-page>
    <f7-navbar>
      <f7-nav-left>
        <f7-link icon-f7="arrow_left" text="Back" v-on:click="goHome"></f7-link>
      </f7-nav-left>
      <f7-nav-title>Equality Level <span v-if="level">{{getLevel}}</span></f7-nav-title>
      <f7-nav-right>
        <f7-link icon-f7="more_vertical" popover-open=".popover-menu"></f7-link>
      </f7-nav-right>
    </f7-navbar>

    <f7-row no-gap>
      <f7-col width="0" tablet-width="20" desktop-width="35"></f7-col>
      <f7-col width="100" tablet-width="60" desktop-width="30">

        <div class="block">
          <f7-row>
            <f7-col class="text-color-white text-align-left"><h2 class="no-margin normal-font">Moves {{moves}}</h2></f7-col>
            <f7-col class="text-color-white text-align-right"><h2 class="no-margin normal-font">{{isWinner ? 'Level Completed' : ''}}</h2></f7-col>
          </f7-row>
        </div>

        <div class="block block-insert">
          <f7-row v-if="grid" no-gap v-for="(cols, rowId) in grid" :key="rowId" class="v-row">
            <f7-col v-for="(col, colId) in cols" :key="colId" :class="'text-align-center v-col v-col-' + col">
              <svgCircle v-if="col[0]" :rowId="rowId" :colId="colId" :data="col[0]" :value="col[1]" :share="col[2]" :callback="updateCount"></svgCircle>
            </f7-col>
          </f7-row>
        </div>

      </f7-col>
      <f7-col width="0" tablet-width="20" desktop-width="35"></f7-col>
    </f7-row>

    <f7-popover class="popover-menu">
      <f7-list>
        <f7-list-item v-on:click="resetPuzzle" title="Reset Puzzle"></f7-list-item>
      </f7-list>
    </f7-popover>
  </f7-page>
</template>
<script>
  import Firebase from 'firebase/app';
  import 'firebase/database';
  import svgCircle from './components/svg-circle';
  import {main} from '../service/main';

  export default {
    components: {
      svgCircle,
    },
    data() {
      return {
        level: this.$f7route.params.levelId || 1,
        moves: 0,
        isWinner: null,
        grid: null
      }
    },
    mounted() {
      this.$f7ready(() => {
        this.$f7.preloader.show();
        this.getProgress();
      })
    },
    computed: {
      getLevel() {
        return parseInt(this.level) + 1;
      }
    },
    methods: {
      getProgress() {
        Firebase.database().ref('puzzle/' + main.getUid() + '/' + this.level).on('value', (resp) => {
          let puzzle = resp.val();

          this.setData(puzzle);

          this.$f7.preloader.hide();
        });
      },

      setData(puzzle) {
        this.grid = puzzle ? puzzle.grid : main.getLevel(this.level);
        this.moves = puzzle ? parseInt(puzzle.moves) : 0;
        this.isWinner = main.checkIfWon(this.grid);

        console.warn(main.getGridCount(this.grid));

        localStorage.setItem('level', JSON.stringify(this.grid));
      },

      updateCount(cell, data, value) {
        this.moves++;
        let grid = JSON.parse(localStorage.getItem('level'));
        let len = data.length;

        // clicked cell
        grid[cell[0]][cell[1]][1] = value - len;

        // adjacent cells
        for (let i = 0; i < data.length; i++) {
          grid[data[i][0]][data[i][1]][1] += 1;
        }

        this.isWinner = main.checkIfWon(grid);

        // if won, show message and go to next level
        if (this.isWinner) {
          main.saveScore(this.level, this.moves).then();

          this.$f7.dialog.alert('All nodes have a positive balance :)', 'You won!', () => {
            this.$f7.views.current.router.navigate('/level/' + (parseInt(this.level) + 1));
          });
        }

        this.saveProgress(this.level, grid, this.isWinner, this.moves);

        this.grid = grid;
      },

      saveProgress(levelId, grid, isWinner, moves) {
        main.saveProgress(levelId, grid, isWinner, moves).then(() => console.log('level saved'));
      },

      resetPuzzle() {
        let grid = main.getLevel(this.level);
        this.isWinner = false;
        this.moves = 0;
        this.grid = grid;

        console.log(this.grid);
        console.log(grid);

        this.saveProgress(this.level, this.grid, this.isWinner, this.moves);

        this.$f7.dialog.alert('Level reset compete');
        this.$f7.popover.close();
      },

      goHome() {
        this.$f7.views.current.router.navigate('/');
      },
    }
  }
</script>



