let config = {
  apiKey: "AIzaSyC63v4ebASO5q1BbCYzjy1AR2jFBPry9Rg",
  authDomain: "equality-abe16.firebaseapp.com",
  databaseURL: "https://equality-abe16.firebaseio.com",
  projectId: "equality-abe16",
  storageBucket: "",
  messagingSenderId: "1006699774233"
};

Firebase.initializeApp(config);

// Import App Component
import App from './app';

// Init F7 Vue Plugin
