<template>
  <f7-page>
    <f7-navbar>
      <f7-nav-title>Equality</f7-nav-title>
    </f7-navbar>
    <f7-row no-gap>
      <f7-col width="0" tablet-width="20" desktop-width="35"></f7-col>
      <f7-col width="100" tablet-width="60" desktop-width="30">

        <f7-row>
          <f7-col width="50" v-if="levels" v-for="(grid, i) in levels" :key="i" v-on:click="goToLevel(i)">

            <div class="block block-insert block-home">
              <f7-row v-if="grid" no-gap v-for="(cols, rowId) in grid" :key="rowId" class="v-row">
                <f7-col v-for="(col, colId) in cols" :key="colId" :class="'text-align-center v-col v-col-' + col">
                  <svgCircle v-if="col[0]" :rowId="rowId" :colId="colId" :data="col[0]" :value="0" :share="col[2]"></svgCircle>
                </f7-col>
              </f7-row>

              <f7-row class="text-color-white text-align-center level-stats">
                <f7-col><span class="level-chip">{{ getMovesCount(i) }} moves</span></f7-col>
                <f7-col><span class="level-chip">rank {{ getRank(i) }}</span></f7-col>
                <f7-col><span class="level-chip">level {{ i + 1 }}</span></f7-col>
              </f7-row>
            </div>

          </f7-col>
        </f7-row>

      </f7-col>
      <f7-col width="0" tablet-width="20" desktop-width="35"></f7-col>
    </f7-row>

  </f7-page>
</template>
<script>
  import Firebase from 'firebase/app';
  import 'firebase/database';
  import {main} from '../service/main';
  import svgCircle from './components/svg-circle';

  export default {
    components: {
      svgCircle,
    },
    data() {
      return {
        cols: 4,
        rows: 6,
        levels: null,
        progress: [],
        score: [],
        // images: [
        //   require('../images/1.jpg'),
        // ]
      }
    },
    mounted() {
      this.$f7ready(() => {
        this.$f7.dialog.close();

        this.getProgress();
        this.getScore();

        this.levels = main.getLevels();
      })
    },
    methods: {
      getProgress() {
        this.$f7.preloader.show();

        this.progress = [];
        Firebase.database().ref('puzzle/' + main.getUid()).on('value', (resp) => {
          this.$f7.dialog.close();
          this.$f7.preloader.hide();

          let puzzles = resp.val();

          if (puzzles) {
            for (let key in puzzles) {
              if (puzzles.hasOwnProperty(key)) {
                this.progress.push({
                  levelId: parseInt(puzzles[key].levelId),
                  moves: parseInt(puzzles[key].moves),
                  isWinner: puzzles[key].isWinner,
                });
              }
            }
          }
        });
      },

      getScore() {
        Firebase.database().ref('score').once('value').then(resp => {
          this.score = resp.val();
        });
      },

      goToLevel(key) {
        localStorage.removeItem('level');
        localStorage.removeItem('moves-' + key);

        this.$f7.views.current.router.navigate('/level/' + key);
      },

      getMovesCount(key) {
        return this.progress[key] ? this.progress[key].moves : 'no';
      },

      getRank(key) {
        let movesCount = this.progress && this.progress[key] ? parseInt(this.progress[key].moves) : 0;

        return this.score && this.score[key] ? movesCount - this.score[key].moves + 1 : '-';
      },
    }
  }
</script>



